import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import {
  Link
} from "react-router-dom";
import { TextField, Typography, InputAdornment, Button, Accordion, AccordionSummary, AccordionDetails } from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
import { titleChanged } from './store/view';
import { allInsulinUnitsChanged, markTaken } from './store/insulinUnits'
import { recordTakeInsulin, recordNoInsulin } from './store/records'
import mergeStates from './utils/mergeStates'
import resetStates from './utils/resetStates';
import makeOnFocusSelect from './utils/makeOnFocusSelect';
import makeOnFocusGotoEndOrSelectZero from './utils/makeOnFocusGotoEndOrSelectZero';
import makeOnChanged from './utils/makeOnChanged';
import makeOnBlur from './utils/makeOnBlur'
import {
  outputContainer,
  topButton
} from './utils/styles';
import {
  Restaurant as RestaurantIcon,
  ExpandMore as ExpandMoreIcon,
  ShowChart as ShowChartIcon,
  FitnessCenter as FitnessCenterIcon,
  Settings as SettingsIcon,
  Autorenew as AutorenewIcon,
  Speed as ProportionateCarbsButtonIcon,
  Info as InfoIcon,
  Check as TakeIcon,
  Close as DontTakeIcon
} from '@material-ui/icons'
import { langs } from './lang/langs';

export const InsulinUnits = () => {
  const state = useSelector(state => state.insulinUnits)
  const settings = useSelector(state => state.settings)
  const lang = langs[settings.lang]
  const values = mergeStates(state, settings)
  const dispatch = useDispatch()

  const onKeyUp = (event) => {
    if (event.keyCode !== 13) return;
    event.target.blur()
  }
  const onFocusGotoEndOrSelectZero = makeOnFocusGotoEndOrSelectZero()
  const onFocusSelect = makeOnFocusSelect()
  const onChanged = makeOnChanged(state, settings, dispatch, allInsulinUnitsChanged)
  const onBlur = makeOnBlur(settings, dispatch, allInsulinUnitsChanged)

  useEffect(() => {
    dispatch(
      titleChanged(lang.insulinUnitsTitle)
    )
  })

  // Information icons
  const [showConsumedCarbs, setShowConsumedCarbs] = useState(false)
  const [showCurrentBG, setShowCurrentBG] = useState(false)
  const [showUnitAdjustmentPercentage, setShowUnitAdjustmentPercentage] = useState(false)
  const [showTargetBg, setShowTargetBg] = useState(false)
  const [showCarbsPerUnit, setShowCarbsPerUnit] = useState(false)
  const [showBgPerUnit, setShowBgPerUnit] = useState(false)

  const calculationType = state.ui_calculationType ?? settings.calculationType

  return (
    <React.Fragment>
    <section className="insulinunits screen__container">

      <div className="insulinunits__output" style={outputContainer}>
        <Typography variant="h2" noWrap>
          {state.roundedAdjustedTotalUnits||0}{lang.insulinUnitsUnitText}
        </Typography>
      </div>

      <div className="insulinunits__calculation_type" style={{display:'flex', marginBottom: '1em'}}>
        <div style={{ flex: 1 }}><input
              disabled={state.ui_isTaken} onChange={onChanged.bind(this, 'calculationType')} id="calc_type_carbs" name="calc_type" type="radio" value="carbs" checked={calculationType === "carbs"} /><label htmlFor="calc_type_carbs">Carbs</label></div>
        <div style={{ flex: 1 }}><input
              disabled={state.ui_isTaken} onChange={onChanged.bind(this, 'calculationType')} id="calc_type_correction" name="calc_type" type="radio" value="correction" checked={calculationType === "correction"} /><label htmlFor="calc_type_correction">Correction</label></div>
        <div style={{ flex: 2 }}><input
              disabled={state.ui_isTaken} onChange={onChanged.bind(this, 'calculationType')} id="calc_type_both" name="calc_type" type="radio" value="both" checked={calculationType === "both"}/><label htmlFor="calc_type_both">Carbs and Correction</label></div>
      </div>

      <div className="insulinunits__button__take" style={{display: 'flex'}}>
        <Button
          className='button'
          style={topButton}
          disabled={state.ui_isTaken || state.roundedAdjustedTotalUnits === 0}
          onClick={
            () => {
              dispatch(recordNoInsulin(state))
              dispatch(markTaken(state))
            }
          }
        >
          <DontTakeIcon />
          &nbsp;{lang.insulinUnitsNoInsulinButtonText}
        </Button>
        <Button
          className='button'
          style={topButton}
          disabled={state.ui_isTaken || state.roundedAdjustedTotalUnits === 0}
          onClick={
            () => {
              dispatch(recordTakeInsulin(state))
              dispatch(markTaken(state))
            }
          }
        >
          <TakeIcon />
          &nbsp;{lang.insulinUnitsTakeInsulinButtonText}
        </Button>
      </div>
      <div className="insulinunits__button__reset">
        <Button
          className='button'
          style={topButton}
          onClick={
            () => {
              dispatch(allInsulinUnitsChanged(resetStates(
                state,
                settings
              )))
            }
          }
        >
          <AutorenewIcon />
          &nbsp;{lang.insulinUnitsResetButtonText}
        </Button>
      </div>

      <div className="insulinunits_input input__group">

        <div className="insulinunits__input__consumedcarbs input__container">
          <TextField label={<div>
            {lang.insulinUnitsConsumedCarbsLabelText}
            {lang.insulinUnitsConsumedCarbsInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowConsumedCarbs(!showConsumedCarbs)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" className="info" style={{display: showConsumedCarbs ? 'flex':'none'}} onClose={() => setShowConsumedCarbs(false)}>
                {lang.insulinUnitsConsumedCarbsInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            disabled={state.ui_isTaken}
            onKeyUp={onKeyUp}
            onFocus={onFocusGotoEndOrSelectZero.bind(this, 'consumedCarbs')}
            onChange={onChanged.bind(this, 'consumedCarbs')}
            onBlur={onBlur.bind(this, 'consumedCarbs')}
            value={values.ui_consumedCarbs}
            InputProps={{
              startAdornment: <InputAdornment position="start"><RestaurantIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">{state.consumedCarbs||0}{lang.insulinUnitsConsumedCarbsUnitText}</InputAdornment>,
            }}
          />
        </div>

        <div className="insulinunits__button__addproportionatecarbs">
          <Button
              disabled={state.ui_isTaken}
              className='button'
              style={topButton}
              component={Link} to="/proportionatecarbs"
            >
              <ProportionateCarbsButtonIcon />
              &nbsp;{lang.insulinUnitsAddProportionateCarbsButtonText}
            </Button>
        </div>

        <div className="insulinunits__input__currentbg input__container">
          <TextField label={<div>
            {lang.insulinUnitsCurrentBgLabelText}
            {lang.insulinUnitsCurrentBgInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowCurrentBG(!showCurrentBG)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" className="info" style={{display: showCurrentBG ? 'flex':'none'}} onClose={() => setShowCurrentBG(false)}>
                {lang.insulinUnitsCurrentBgInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            disabled={state.ui_isTaken}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'currentBg')}
            onChange={onChanged.bind(this, 'currentBg')}
            onBlur={onBlur.bind(this, 'currentBg')}
            value={values.ui_currentBg}
            InputProps={{
              startAdornment: <InputAdornment position="start"><ShowChartIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">{lang.insulinUnitsCurrentBgUnitText}</InputAdornment>,
            }}
          />
        </div>

        <div className="insulinunits__input__unitadjustmentpercentage input__container">
          <TextField label={<div>
            {lang.insulinUnitsUnitAdjustmentPercentageLabelText}
            {lang.insulinUnitsUnitAdjustmentPercentageInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowUnitAdjustmentPercentage(!showUnitAdjustmentPercentage)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" className="info" style={{display: showUnitAdjustmentPercentage ? 'flex':'none'}} onClose={() => setShowUnitAdjustmentPercentage(false)}>
                {lang.insulinUnitsUnitAdjustmentPercentageInfoText}
              </Alert>
            </React.Fragment>
            }
          </div>}
            disabled={state.ui_isTaken}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'unitAdjustmentPercentage')}
            onChange={onChanged.bind(this, 'unitAdjustmentPercentage')}
            onBlur={onBlur.bind(this, 'unitAdjustmentPercentage')}
            value={values.ui_unitAdjustmentPercentage}
            InputProps={{
              startAdornment: <InputAdornment position="start"><FitnessCenterIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">{lang.insulinUnitsUnitAdjustmentPercentageUnitText}</InputAdornment>,
            }}
          />
        </div>

        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <SettingsIcon />
            &nbsp;{lang.insulinUnitsPersonalisedSettingsButtonText}
          </AccordionSummary>
          <AccordionDetails>

            <div className="insulinunits__input__targetbg input__container">
              <TextField label={<div>
                {lang.insulinUnitsTargetBgLabelText}
                {lang.insulinUnitsTargetBgInfoText &&
                <React.Fragment>
                  <Button className="btn-info" onClick={() => setShowTargetBg(!showTargetBg)}>
                    <InfoIcon />
                  </Button>
                  <Alert severity="info" className="info" style={{display: showTargetBg ? 'flex':'none'}} onClose={() => setShowTargetBg(false)}>
                    {lang.insulinUnitsTargetBgInfoText}
                  </Alert>
                </React.Fragment>
                }
              </div>}
                disabled={state.ui_isTaken}
                onKeyUp={onKeyUp}
                onFocus={onFocusSelect.bind(this, 'targetBg')}
                onChange={onChanged.bind(this, 'targetBg')}
                onBlur={onBlur.bind(this, 'targetBg')}
                value={values.ui_targetBg}
                InputProps={{
                  startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
                  endAdornment: <InputAdornment position="end">{lang.insulinUnitsTargetBgUnitText}</InputAdornment>,
                }}
              />
            </div>

            <div className="insulinunits__input__carbsperunit input__container">
              <TextField label={<div>
                {lang.insulinUnitsCarbsPerUnitLabelText}
                {lang.insulinUnitsCarbsPerUnitInfoText &&
                  <React.Fragment>
                  <Button className="btn-info" onClick={() => setShowCarbsPerUnit(!showCarbsPerUnit)}>
                    <InfoIcon />
                  </Button>
                  <Alert severity="info" className="info" style={{display: showCarbsPerUnit ? 'flex':'none'}} onClose={() => setShowCarbsPerUnit(false)}>
                    {lang.insulinUnitsCarbsPerUnitInfoText}
                  </Alert>
                  </React.Fragment>
                }
              </div>}
                disabled={state.ui_isTaken}
                onKeyUp={onKeyUp}
                onFocus={onFocusSelect.bind(this, 'carbsPerUnit')}
                onChange={onChanged.bind(this, 'carbsPerUnit')}
                onBlur={onBlur.bind(this, 'carbsPerUnit')}
                value={values.ui_carbsPerUnit}
                InputProps={{
                  startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
                  endAdornment: <InputAdornment position="end">{lang.insulinUnitsCarbsPerUnitText}</InputAdornment>,
                }}
              />
            </div>

            <div className="insulinunits__input__bgperunit input__container">
              <TextField label={<div>
                {lang.insulinUnitsBgPerUnitLabelText}
                {lang.insulinUnitsBgPerUnitInfoText &&
                  <React.Fragment>
                    <Button className="btn-info" onClick={() => setShowBgPerUnit(!showBgPerUnit)}>
                      <InfoIcon />
                    </Button>
                    <Alert severity="info" className="info" style={{display: showBgPerUnit ? 'flex':'none'}} onClose={() => setShowBgPerUnit(false)}>
                      {lang.insulinUnitsBgPerUnitInfoText}
                    </Alert>
                  </React.Fragment>
                }
              </div>}
                disabled={state.ui_isTaken}
                onKeyUp={onKeyUp}
                onFocus={onFocusSelect.bind(this, 'bgPerUnit')}
                onChange={onChanged.bind(this, 'bgPerUnit')}
                onBlur={onBlur.bind(this, 'bgPerUnit')}
                value={values.ui_bgPerUnit}
                InputProps={{
                  startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
                  endAdornment: <InputAdornment position="end">{lang.insulinUnitsBgPerUnitUnitText}</InputAdornment>,
                }}
              />
            </div>
          </AccordionDetails>
        </Accordion>

      </div>

    </section>
  </React.Fragment>
  )
}
