import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { Button } from '@material-ui/core';
import { titleChanged } from './store/view';
import { resetRecords } from './store/records'
import {
  outputContainer,
  topButton
} from './utils/styles';
import {
  Autorenew as AutorenewIcon
} from '@material-ui/icons'
import { langs } from './lang/langs';
import { LineChart, CartesianGrid, XAxis, YAxis, Tooltip, Legend, Line, Bar, ComposedChart, ResponsiveContainer } from 'recharts';

export const Records = () => {
  const state = useSelector(state => state.records)
  const settings = useSelector(state => state.settings)
  const lang = langs[settings.lang]
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(
      titleChanged(lang.recordsTitle)
    )
  })



  const now = Date.now()
  const oneHour = 60 * 60 * 1000
  const endDate = now

  const data = state.map(row => {
    return {
      date: row.data,
      hour: (row.date - endDate) / oneHour,
      units: row.roundedAdjustedTotalUnits,
      carbs: row.consumedCarbs,
      glucose: row.currentBg
    }
  }).filter(row => row.hour > -48)

  return (
    <React.Fragment>
    <section className="insulinunits screen__container">

      <div className="insulinunits__output" style={outputContainer}>
        <ResponsiveContainer width={"100%"} height={300}>
        <ComposedChart
            data={data}
            margin={{
              top: 2,
              right: 2,
              bottom: 2,
              left: 2
            }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="hour" type="number" label="last 24 hours" domain={[-24, 0]} interval={oneHour}/>
          <YAxis yAxisId='left' label={{value:"carbs", angle: -90 }}/>
          <YAxis yAxisId="right" orientation='right' label={{value:"units and glucose", angle: 90}}/>
          <Tooltip labelFormatter={(value, name, props) => {
            return new Date(endDate+(value*oneHour)).toLocaleDateString("en-GB", { weekday: 'short', day: 'numeric', month: 'short', hour: 'numeric', minute: 'numeric' })
          }}/>
          <Legend />
          <Bar yAxisId="left" dataKey="carbs" fill="green" />
          <Line yAxisId="right" type="monotone" dataKey="glucose" stroke="blue" />``
          <Bar yAxisId="right" dataKey="units" fill="red" />
        </ComposedChart>
        </ResponsiveContainer>
      </div>

      <div className="insulinunits__button__reset">
        <Button
          className='button'
          style={topButton}
          onClick={
            () => {
              dispatch(resetRecords(state))
            }
          }
        >
          <AutorenewIcon />
          &nbsp;{lang.insulinUnitsResetButtonText}
        </Button>
      </div>

      <div className="insulinunits_input input__group">
          <table>
            <thead>
            <tr>
              <th>Date</th>
              <th>Glucose</th>
              <th>Carbs</th>
              <th>Units</th>
              <th>For</th>
            </tr>
            </thead>
            {state.map((row, index) => {
              return <tr key={index}>
                <td>{new Date(row.date).toLocaleDateString("en-GB", { weekday: 'short', day: 'numeric', month: 'short', hour: 'numeric', minute: 'numeric' })}</td>
                <td>{row.currentBg}mmol/l</td>
                <td>{row.consumedCarbs}g</td>
                <td>{row.calculationType === null ? 'none' : row.roundedAdjustedTotalUnits}</td>
                <td>{row.calculationType === null ? 'n/a' : row.calculationType}</td>
              </tr>
            })}
          </table>

      </div>

    </section>
  </React.Fragment>
  )
}
